<script>
  export let options, name = "", id = "";
  export let value = options[0]
  let show = false
  let select = (event) => {
    value = event.target.innerHTML
    show = !show
  }
</script>
<style>
  .select-box {
    margin-bottom: 7px;
  }
  .select-box, .select {
    position: relative;
    display: flex;
    font-size: 0.9em;
  }
  .select {
    width: fit-content;
    height: 40px;
    border: 1px solid #f3f3f3;
  }
  .select-value {
    padding: 10px 20px;
    border: 0;
    border-radius: 0px;
    max-width: 200px;
    height: 40px;
    background-color: #f3f3f3;
    color: #363636;
  }
  .select-options {
    position: absolute;
    top: 40px;
    z-index: 999;
    width: calc(100% - 40px);
  }
  .option {
    padding: 0 20px;
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    background-color: #f3f3f3;
    cursor: pointer;
    box-sizing: border-box;
  }
  .select-arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #e4e4e4;
    cursor: pointer;
  }
  .label {
    padding: 0 20px;
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-color: #fbfbfb;
  }
</style>
<div class="select-box">
  <div class="select">
    <input type="text" class="select-value" disabled bind:value={value} {name} {id}>
    {#if show}
      <div class="select-options">
        {#each options as option}
          <div class="option" on:click={select}>{option}</div>
        {/each}
      </div>
    {/if}
    <div class="select-arrow" on:click={()=>show = !show}><i class="fas {show ? 'fa-chevron-up' : 'fa-chevron-down'}"></i></div>
  </div>
</div>