<script>
  export let tabs;
  import { onMount } from 'svelte';

  let currentTab = ''
  export let value = ''
</script>
<style>
  .tabs {
    display: flex;
  }
  .tab {
    margin-right: 10px;
    padding: 10px 50px;
    background-color: #fbfbfb;
    text-transform: uppercase;
    font-size: 0.8em;
    cursor: pointer;
  }
  .blocks {
    border: 1px solid #f9f9f9;
    border-top: 2px solid #e4e4e4;
    width: fit-content;
    display: flex;
    justify-content: flex-start;
  }
  .block {
    position: relative;
    padding: 10px 20px;
    background-color: #fbfbfb;
    text-transform: uppercase;
    font-size: 0.7em;
    cursor: pointer;
  }
  .block:not(:last-child):after {
    content: " ";
    position: absolute;
    top: 20%;
    right: 0;
    width: 1px;
    height: 60%;
    background-color: #e4e4e4;
  }
  .tab--selected, .tab:hover, .block:hover, .block--selected {
    background-color: #e4e4e4;
    font-weight: bold;
  }
</style>
<div class="tabs">
  {#each Object.keys(tabs) as tab}
    <div class="tab {currentTab === tab ? 'tab--selected' : ''}" on:click={()=>currentTab=tab}>{tab}</div>
  {/each}
</div>
{#if currentTab}
  <div class="blocks">
      {#each tabs[currentTab] as block}
        <div class="block {value === block ? 'block--selected' : ''}" on:click={()=>value=block}>{block}</div>
      {/each}
  </div>
{/if}