<script>
  import Checkbox from './Checkbox.svelte';
  import Slider from './Slider.svelte';
  import Select from './Select.svelte';
  import Tabs from './Tabs.svelte';
  let tabs = {
    'видео': ['mp4', 'avi', 'mpeg', 'mov', 'flv', '3pg', 'webm', 'mkv', 'apple', 'android'],
    'аудио': ['mp3']
  }
  let size = ["1920x1080", "1080x720", "480x800"]
  let vcodecs = ['Xvid']
  let acodecs = ['mp3']
  let vbitrate = ['15000']
  let abitrate = ['256']
  let format = ""
  let file = ""
  let convert = (event) => {
    alert(`VCodec - ${document.getElementById('video-codec').value}
      ACodec -  ${document.getElementById('audio-codec').value}
      Size -  ${document.getElementById('size').value}
      vbitrate -  ${document.getElementById('vbitrate').value}
      abitrate -  ${document.getElementById('abitrate').value}
      quality -  ${document.getElementById('quality').value}
      mute -  ${document.getElementById('mute').value}
      format -  ${format}
      file -  ${file}
      `)
  }
  let upload = (event) => {
    file = event.target.value
  }
</script>
<style>
  header {
    padding: 0 10%;
    height: 50px;
    display: flex;
    align-items: center;
  }
  .container {
    padding: 0 10%;
  }
  .steps {
    background-color: #f6f6f6;
  }
  .feactures {
    padding: 40px 0;
  }
  .title {
    padding: 10px 0;
    text-align: center;
  }
  .title > span {
    font-size: 1.8em;
  }
  .title > p {
    margin: 0;
    color: #95a5a6;
  }
  .box {
    padding-bottom: 15px;
    display: flex;
  }
  .bg-white {
    background-color: #fff;
  }
  .box-content {
    padding: 15px 20px 0 20px;
    width: 100%;
  }
  .step {
    padding: 2px 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 60px;
    font-size: 1.5em;
    font-weight: bold;
    text-transform: uppercase;
    color: #bdc3c7;
  }
  .box--selected > .step {
    color: #5d858d;
  }
  .box--selected > .box-content {
    box-shadow: 0 0 20px 5px rgba(0,0,0,.1);
  }
  .info {
    margin-bottom: 10px;
    font-size: 0.9em;
  }
  .action {
    margin-bottom: 15px;
  }
  button {
    cursor: pointer;
  }
  .btn-upload {
    padding: 5px 40px;
    border: 0;
    display: inline-block;
    text-align: center;
    width: max-content;
    font-weight: bold;
    font-size: 0.75em;
    color: #fff;
    text-transform: uppercase;
    background-color: #ff6a38;
    cursor: pointer;
  }
  .input-upload {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: 1;
  }
  .btn-convert {
    margin-left: 20px;
    padding: 10px 30px;
    border: 0;
    background-color: #e4e4e4;
    text-transform: uppercase;
    font-size: 1.2em;
    font-weight: bold;
  }
  .downloaded {
    padding: 15px;
    color: #5d858d;
    font-size: 0.8em;
  }
  .box-input {
    display: flex;
  }
  .box-input > label {
    padding: 0 20px;
    width: 200px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 0.8em;
    background-color: #fbfbfb;
  }
  .row {
    margin: 0;
  }
  .feacture > img {
    margin: auto;
    grid-area: img;
  }
  .feacture > h4 {
    margin: 0;
    grid-area: title;
    color: #5c858d;
  }
  .feacture > p {
    margin: 0;
    grid-area: des;
  }
  .feacture {
    display: grid;
    grid-template-columns: 100px auto;
    grid-template-rows: 30px auto;
    grid-template-areas:
      "img title"
      "img des"
  }
  .feactures .row, .feactures .title {
    margin-bottom: 40px;
  }
  footer {
    padding: 20px;
    background-color: #f6f6f6;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>
<header>
  <div class="logotype"><strong>ВидеоКонвертер</strong></div>
</header>
<main>
  <section class="steps">
    <div class="container">
      <div class="title">
        <span><strong>Онлайн ВидеоКонвертер</strong> на русском языке</span>
        <p>Позволяет быстро и безопасно конвертировать 100+ видео- и аудиоформатов</p>
      </div>
      <div class="box box--selected">
        <div class="step"><span>1</span>шаг</div>
        <div class="box-content bg-white">
          <div class="info">
            <strong>Загрузите ваш файл</strong>, нажав на кнопку и выбрав подходящий файл на вашем устройстве
          </div>
          <div class="action">
            <label class="btn-upload" for="file"><i class="fas fa-upload"></i> Загрузить файл</label>
            <input class="input-upload" type="file" id="file" on:change={upload}>
            <span class="downloaded"><i class="far fa-check-circle"></i> Данные успешно загружены</span>
          </div>
        </div>
      </div>
      <div class="box">
        <div class="step"><span>2</span>шаг</div>
        <div class="box-content bg-white">
          <div class="info">
            Выберите <strong>нужный формат</strong> для конвертации
          </div>
          <div class="action">
            <Tabs {tabs} bind:value={format}/>
          </div>
          <div class="info">
            <strong>Настройте</strong> необходимый видео- и аудиоформат
          </div>
          <div class="action">
            <div class="row">
              <div class="box-input col-5">
                <label>Разрешение</label>
                <Select options={size} id="size"/>
              </div>
              <div class="box-input col-7">
                <label>Качество, Кбит</label>
                <Slider min="0" max="10000" id="quality"/>
              </div>
            </div>
            <div class="row">
              <div class="box-input col-5">
                <label>Видео-кодек</label>
                <Select options={vcodecs} id="video-codec"/>
              </div>
              <div class="box-input col-5">
                <label>Аудио-кодек</label>
                <Select options={acodecs} id="audio-codec"/>
              </div>
              <div class="box-input col-2">
                <Checkbox id="mute" text="без аудио"/>
              </div>
            </div>
            <div class="row">
              <div class="box-input col-5">
                <label>Битрейт, Кбит</label>
                <Select options={vbitrate} id="vbitrate"/>
              </div>
              <div class="box-input col-5">
                <label>Битрейт, Кбит</label>
                <Select options={abitrate} id="abitrate"/>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="box">
        <div class="step"><span>3</span>шаг</div>
        <div class="box-content">
          <div class="info">
            Для начала конвертации <strong>нажмите на кнопку: </strong> <button class="btn-convert"  on:click={convert}><i class="fas fa-retweet"></i> конвертировать</button>
          </div>
        </div>
      </div>
    </div>
  </section>
  <section class="feactures">
    <div class="container">
      <div class="title">
        Позволяет <strong>быстро и безопасно конвертировать</strong> видео- и аудио форматы
      </div>
      <div class="row">
        <div class="col-4">
          <div class="feacture">
            <img src="./formats.png" alt="formats">
            <h4>100+ форматов</h4>
            <p>Быстрая конвертация в популярные видео- и аудиоформаты</p>
          </div>
        </div>
        <div class="col-4">
          <div class="feacture">
            <img src="./devices.png" alt="devices">
            <h4>300 устройств</h4>
            <p>Создание видео для телефонов, планшетов и сайтов</p>
          </div>
        </div>
        <div class="col-4">
          <div class="feacture">
            <img src="./users.png" alt="users">
            <h4>57+ тыс. пользователей</h4>
            <p>Мы предоставляем услуги по конвертации видео с 2011 года</p>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-4">
          <div class="feacture">
            <img src="./free.png" alt="free">
            <h4>Бесплатная конвертация</h4>
            <p>Приложение полностью бесплатное. Регистрация не требуется</p>
          </div>
        </div>
        <div class="col-4">
          <div class="feacture">
            <img src="./files.png" alt="files">
            <h4>Любое количество файлов</h4>
            <p>В приложении нет ограничений по количеству файлов для загрузки</p>
          </div>
        </div>
        <div class="col-4">
          <div class="feacture">
            <img src="./guard.png" alt="guard">
            <h4>Безопастность гарантирована</h4>
            <p>Все файлы удаляются с сервера после завершения работы</p>
          </div>
        </div>
      </div>
    </div>
  </section>
</main>
<footer>
  &copy; 2007-2019 ALULU Software. Все права защищены.
</footer>