<script>
  export let text, value = false , id
</script>
<style>
  .checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  .checkbox-checkbox {
    margin-right: 10px;
    width: 15px;
    height: 15px;
    border-radius: 3px;
    background-color: #f3f3f3;
  }
  .checkbox-input {
    position: relative;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: 1;
  }
  .checkbox-checked:after {
    content: " ";
    position: absolute;
    top: 16px;
    left: 4px;
    width: 7px;
    height: 12px;
    border-right: 3px solid #999;
    border-bottom: 3px solid #999;
    transform: rotate(45deg);
  }
</style>
<div class="checkbox" on:click={()=>value = !value}>
  <label for={id} class="checkbox-checkbox {value ? 'checkbox-checked' : ''}" on:click={()=>value = !value}></label>
  <input type="checkbox" class="checkbox-input" id={id} checked={value} value={value}>
  {text}
</div>